<template>
  <div class="shelves-content">
    <div class="empty" v-if="!showTable">
      <h3>请在左侧选择一个仓库库位</h3>
    </div>
    <div class="content" v-else>
      <el-row :span="24">
        <el-col>
          <el-button type="primary" @click.native="qrCodePrint">二维码打印</el-button>
          <common-table>
            <template slot="table">
              <el-table
                ref="shelvesTable"
                stripe
                :border="true"
                :data="leafNode.slice((pagination.pageNum-1)*pagination.pageSize,pagination.pageNum*pagination.pageSize)"
                @selection-change="selected => selectRow = selected">
                <!--<el-table-column type="selection" width="30" ></el-table-column>-->
                <el-table-column type="index" :index="indexMethod" label="序号" width="50"></el-table-column>
                <el-table-column sortable prop="name" label="库位名称" min-width="120"></el-table-column>
                <el-table-column prop="storageType" label="库位类型" :formatter="formatType"></el-table-column>
                <el-table-column prop="storageStatus" label="库位状态" :formatter="formatOpen"></el-table-column>
                <el-table-column sortable prop="size" label="容量(米²)" min-width="80"></el-table-column>
                <el-table-column sortable prop="length" label="长度(米)" min-width="80"></el-table-column>
                <el-table-column sortable prop="width" label="宽度(米)" min-width="80"></el-table-column>
                <el-table-column sortable prop="wareHouseName" label="所属仓库"></el-table-column>
                <el-table-column prop="desc" label="描述" min-width="120" :show-overflow-tooltip="true"></el-table-column>
              </el-table>
            </template>
            <template slot="pagination">
              <el-pagination   background   layout="total, prev, pager, next"
                               :current-page.sync="pagination.pageNum"
                               :page-size="pagination.pageSize"
                               :total="pagination.total"
                               @current-change="changePage"
              ></el-pagination>
            </template>
          </common-table>
        </el-col>
      </el-row>
    </div>
    <!-- 二维码打印 -->
    <qr-code
      :show.sync="qrCodeVisible"
      :tableData="leafNode"
    />
  </div>
</template>

<script>
  import CommonWrapper from '@/components/commonWrapper'
  import CommonTable from '@/components/commonTable'
  import NavButton from '@/components/navButton'

  import QrCode from './QrCode'

  export default {
    name: 'ShelvesDetail',
    data () {
      return {
        showTable: false,
        selectRow: [],
        tableData: [],
        leafNode: [],
        qrCodeVisible: false,
        pagination: { // 翻页数据
          pageNum: 1,
          pageSize: 10,
          total: 0
        },
      }
    },
    props: {
      nodeData: {
        type: Object,
        required: true
      },
      isInit: {
        type: Boolean
      }
    },
    watch: {
      nodeData: {
        handler (nodeData) {
          this.getDetail()
        },
        deep: true
      }
    },
    methods: {
      formatOpen (row) {
        return row.storageStatus === '1' ? '启用' : '禁用'
      },
      formatType (row) {
        switch (row.storageType.toString()) {
          case '1' :
            return '普通库位'
            break
          case '2' :
            return '收货区'
            break
          case '3' :
            return '备货区'
            break
          case '4' :
            return '质检区'
            break
          case '5' :
            return '待收货区'
            break
          case '6' :
            return '维修区'
            break
          case '7' :
            return '待发货区'
            break
          case '8' :
            return '发货区'
            break

        }
      },
      getDetail () {
        if (this.nodeData) {
          this.leafNode = []
          this.getLeafNode(this.nodeData)
          this.showTable = true
        }
      },
      getLeafNode (nodeData) {
        if (nodeData['children'].length) {
          for (let child of nodeData['children']) {
            this.getLeafNode(child)
          }
        } else {
          this.leafNode.push(nodeData)
        }
        this.pagination.total = this.leafNode.length
      },
      // 列表翻页
      changePage (pageNum) {
        this.pagination.pageNum = pageNum
      },
      // 生成列表序号
      indexMethod (index) {
        return (index + 1) + (this.pagination.pageNum - 1) * this.pagination.pageSize
      },
      isButtonIsDisabled () {
        return !!this.selectRow.length
      },
      // 菜单 -- 新增
      handleAdd () {
        this.$emit('update:isCreate', true)
        this.$emit('openDialog', {})
      },
      // 菜单 -- 编辑
      edit () {
        this.$emit('update:isCreate', false)
        this.$emit('openDialog', [this.selectRow])
      },
      cancel () {
        this.$confirm('确认删除选中的库位吗?', '提示', {
          type: 'warning'
        }).then(() => {
          let params = {
            shelvesId: []
          }
          for (let item of this.selectRow) {
            params.shelvesId.push(item.id)
          }
          this.$emit('deleteShelvesList', params)
        }).catch(() => {
        })
      },
      qrCodePrint () {
        this.qrCodeVisible = true
      }
    },
    components: {
      CommonWrapper,
      CommonTable,
      NavButton,
      QrCode
    }
  }
</script>

<style lang="scss" scoped>
  .shelves-content {
    display: flex;
    flex-direction: column;
    align-items: center;

    .empty {
      padding: 30px;
      font-size: 2rem;
      color: darken($base-light-color, 15%);
    }
    .content {
      width: 99%;
      max-width: 1200px;
    }
  }
</style>



